/**
 * 格式化月份（2021-09）
 */
export function formatMonth () {
  var year = new Date().getFullYear().toString();
  var month = (new Date().getMonth() + 1).toString().padStart(2, "0");
  var currentMonth = year + "-" + month;
  return currentMonth;
}

/**
 * 格式化日份（2021年9月17日）
 */
export function formatDate () {
  var year = new Date().getFullYear().toString();
  var month = (new Date().getMonth() + 1).toString().padStart(2, "0");
  var date = new Date()
    .getDate()
    .toString()
    .padStart(2, "0");
  var currentDate = year + "年" + month + "月" + date + "日";
  return currentDate;
}

/**
 * 转换时间（2021-9）
 */
export function formatDateTime (datetime, type = "2") {
  var year = datetime.getFullYear().toString();
  var month = (datetime.getMonth() + 1).toString().padStart(2, "0");
  var date = datetime
    .getDate()
    .toString()
    .padStart(2, "0");
  var currentDate = "";
  if (type == "2") {
    // 格式化为月
    currentDate = year + "-" + month;
  } else if (type == "1") {
    // 格式化为年
    currentDate = year;
  }
  return currentDate;
}

/**
 * 获取上个月
 */
export function getLastMonth() {
    var date = new Date();
    var year = date.getFullYear();   //当前年：四位数字
    var month = date.getMonth();     //当前月：0-11

    if (month == 0) {   //如果是0，则说明是1月份，上一个月就是去年的12月
        year -= 1;
        month = 12;
    }

    month = month < 10 ? ('0' + month) : month;   //月份格式化：月份小于10则追加个0

    let lastYearMonth = year + '-' + month;

    return lastYearMonth;
}

/**
 * 生成一个带参数的url
 */
export function createUrl (obj) {
  var length = obj && obj.length,
    idx = 0,
    url = obj.url + '?';
  for (var key in obj) {
    if (key != 'url' && obj[key] !== null) {
      url += (key + '=' + encodeURIComponent(obj[key]) + '&');
    }
  }
  var result = ""
  if (url.lastIndexOf('&') > -1) {
    result = url.substring(0, url.lastIndexOf('&'))
  }
  if (url.lastIndexOf('?') > -1) {
    result = url.substring(0, url.lastIndexOf('?'))
  }
  return result;
}